import React from 'react';

import { Container } from '../../../../shared/ui/Layout';
import { AppImage } from '../../../../shared/ui/AppImage';
import { formatDateAsDDMMYY } from '../../../../shared/lib/utils';

import imageStub from '../../../../images/basket/Stub_132_128.jpg';

import './OrderItem.scss';

const OrderItem = ({ order }) => {
  const {
    id,
    user,
    status,
    created_at,
    order_products: [
      {
        product: {
          id: product_id,
          supplier: { id: supplier_id, name: supplier_name },
          sku,
          name: product_name,
          image,
        },
        quantity,
        price,
        cost,
      },
    ],
  } = order;

  return (
    <Container direction="row" className="order-item" border>
      <div className="order-item__header">
        <div className="order-item__additional-info">
          <div className="order-item__order-date">{formatDateAsDDMMYY(created_at)}</div>
          <div className="order-item__order-number">№{id}</div>
        </div>
        <div className="order-item__company-info">
          <AppImage
            className="order-item__company-logo"
            radius="radius-50"
            border
            imageSrc={imageStub}
          />
          <div className="order-item__company-name">Планета</div>
        </div>
      </div>
      <div className="order-item__delevery-info">
        <div className="order-item__delevery-date">
          <div className="">Ориентировочная дата доставки:</div>
          <div className="">14.08.23</div>
        </div>
      </div>
    </Container>
  );
};

export { OrderItem };
