import React from 'react';
import PropTypes from 'prop-types';

import { OrdersListHeader } from '../OrdersListHeader';
import { OrderItem } from '../OrderItem';
import { Container } from '../../../../shared/ui/Layout';

import './OrdersList.scss';

/**
 * Компонент для отображения списка заказов.
 *
 * @param {Array} orders - Массив заказов для отображения.
 * @returns {JSX.Element}
 */
const OrdersList = ({ orders }) => {
  const viewOrdersList = () => {
    return (
      <Container className="orders-list">
        <OrdersListHeader />
        {/* eslint-disable-next-line jsx-a11y/no-redundant-roles */}
        <ul role="list" className="orders-list__list">
          {orders?.map((order) => (
            <li className="orders-list__item" key={order.id}>
              <OrderItem order={order} />
            </li>
          ))}
        </ul>
      </Container>
    );
  };

  const viewNotFound = () => {
    return <div className="favorites__empty">Мои заказы пусто</div>;
  };

  return orders && orders.length ? viewOrdersList() : viewNotFound();
};

OrdersList.propTypes = {
  orders: PropTypes.array.isRequired,
};

export { OrdersList };
